body {
    font-family: monospace;
    margin: 0;
}
.lab1-box {
    max-width: 1000px;
    margin: auto;
    border-bottom: 1px solid #777;
}

.lab2-box {
    max-width: 1000px;
    margin: auto;
}


.title {
    font-size: 26px;
    padding-top: 32px;
    padding-bottom: 0px;
    margin: 0;
}
.text {
    margin: 16px 0;
}
.head-row {
    background-color: #ccc;
}
.head-row, .row {
    display: flex;
    justify-content: space-between;
}

.head-item, .item {
    text-align: center;
    border: 1px solid #777;
    width: calc(100% / 8);
    border-bottom: 1px solid transparent;
    width: 18%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 24px;
}

.item > span {
    width: 25%;
}

.head-item + .head-item, .item + .item {
    border-left: 1px solid transparent;

}

.small {
    width: 3%;
}

.grey {
    background-color: #eee;
}

.area {
    resize: none;
    -webkit-appearance: none;   
    appearance: none;
    cursor: pointer;
    border: 1px solid #222;
    border-radius: 8px;
    padding-top: 6px;
    padding-left: 6px;
    outline: none;

    transition: 0.3s;

    display: block;
    margin: 16px 0 8px;
    width: 200px;
    box-sizing: border-box;
}

.area:focus {
    border-color: #999;
}

.btn {
    display: block;
    -webkit-appearance: none;   
    appearance: none;
    cursor: pointer;
    border: none;
    background-color: #222;
    border-radius: 8px;
    width: 200px;
    height: 32px;
    transition: 0.3s;
    color: #fff;
}

.btn:hover {
    background-color: #333;
    box-shadow: 1px 1px 0 0 #999;
}

.table-min {
    margin-bottom: 36px;
    margin-top: 36px;
}

.box {
    border: 1px solid #aaa;
    width: fit-content;
    border-radius: 4px;
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.1);
    padding: 8px;
}

.row-min {
    display: flex;
    height: 36px;
    align-items: center;
    width: fit-content;
}

.item-min {
    width: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.title-min {
    font-size: 13px;
    font-weight: 400;
    margin-bottom: 8px;
}

.bold {
    font-weight: bold;
}

.flex-box {
    display: flex;
    flex-wrap: wrap;   
}

.offset {
    width: calc(100% / 3 - 20px);
    margin-left: 10px;
    margin-right: 10px;
}

.inner {
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
}

.header {
    height: 64px;
    background-color: #fff;
    box-shadow: 0 0 10px 2px rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
}

.nav {
    width: 400px;
    display: flex;
    justify-content: space-between;
}

.link {
    font-size: 15px;
    text-decoration: none;
    transition: 0.3s;
    color: #222;
    border-bottom: 1px solid transparent;
    padding: 10px 20px;
}

.link:hover, .active {
    border-bottom: 1px solid #000;
}